const Card = require('./Card');

class CardView {
  static set imagePath(path) {
    CardView._imagePath = path;
  }

  static get imagePath() {
    return CardView._imagePath || 'images/';
  }
  constructor(cardData, imagePath = CardView.imagePath) {
    const containerEl = document.createElement('div');
    // const imgEl = document.createElement('img');

    containerEl.className = `card ${cardData.suit}-${cardData.rank}`;
    // containerEl.addClass(`${cardData.suit}-${cardData.rank}`);
    // containerEl.attributes['data-suit'] = cardData.suit;
    // containerEl.attributes['data-rank'] = cardData.rank;
    // imgEl.src = `${imagePath}${cardData.imageName}`;
    // imgEl.src = `imageZSs/${cardData.imageName}`;
    // imgEl.alt = cardData.description;
    // containerEl.appendChild(imgEl);

    this._data = cardData.clone();
    // this._imagePath = imagePath;
    this._container = containerEl;
    // this._image = imgEl;
  }

  get cardData() {
    return this._data.clone();
  }

  get element() {
    return this._container;
  }
}

module.exports = CardView;
